}

.list, ul, li, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

section {
  padding: 0 15px;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.no-scroll {
